import { specialMovesEnum } from '../movegen/Board';
import crc32 from './CRC32';
import { parseSingleCoordinate } from './Parsers';

const lineSymbols = {
    lineStart: Symbol(),
    lineEnd: Symbol()
};

const moveTypesSpecial = {
    castling: Symbol()
};

function createMoveDataFromPGN(moveString) {
    const move = {
        startCoordinates: [],
        endCoordinates: []
    };
    
    if (/O-O-?O?.*/.test(moveString)) {
        move.specialType = moveTypesSpecial.castling;
    } else {
        const coordinates = moveString.match(/[A-Z-]([a-n]\d{1,2})(?:-|x)[A-Z-]?([a-n]\d{1,2})/);
        move.startCoordinates = parseSingleCoordinate(coordinates[1]);
        move.endCoordinates = parseSingleCoordinate(coordinates[2]);
    }

    return move;
}

function createMoveTree() {
    const getSelection = (root, path) => {
        let currentSelection = root;
        for (let i = 0; i < path.length; i++) {
            if (Array.isArray(currentSelection)) {
                currentSelection = currentSelection[path[i]];
            } else {
                if (currentSelection?.alternativeLines) {
                    currentSelection = currentSelection?.alternativeLines[path[i]];
                } else {
                    currentSelection.alternativeLines = [];
                    currentSelection = currentSelection.alternativeLines;
                }

                if (!currentSelection) return undefined;
            }
        }

        return currentSelection;
    };

    const generateObject = (object, current) => {
        let suffix = '';
        const stripRegex = /\+#/g;
        switch (current[0]) {
            case 'moveName':
                const stripped = current[1].replace(stripRegex, '');
                object.moveName = stripped;
                object.moveData = createMoveDataFromPGN(stripped);
                break;
            case 'type':
                object.isResigned = current[1].type === specialMovesEnum.resign ?? false;
                object.isPassing = current[1].type === specialMovesEnum.pass ?? false;
                object.isTimeout = current[1].type === specialMovesEnum.timeout ?? false;
                object.isStalemate = current[1].type === specialMovesEnum.stalemate ?? false;
                object.isDraw = current[1].type === specialMovesEnum.draw ?? false;
                break;
            case 'comment':
                object.comment = current[1];
                break;
            case 'suffix':
                suffix = current[1].replace(stripRegex, '');
                if (suffix.startsWith('=')) {
                    object.isPromotion = suffix.slice(1);
                }
                break;
        }
        if (suffix && object.moveName) object.moveName = object.moveName + suffix;
    }

    return {
        moves: [],
        currentMove: [0],
        boardHashes: new Map(),
        setNewMove(path, move) {
            if (typeof move !== 'object') return;
            const selection = getSelection(this.moves, path);

            if (selection) {
                if (Array.isArray(selection)) {
                    selection.push(move[1]);
                } else {
                    selection.alternativeLines = [];
                    selection.alternativeLines.push([move[1]]);
                }
            }
        },
        getMove(path) {
            const selection = getSelection(this.moves, path);
            let object = {};
            if (selection && typeof path[path.length - 1] === 'string') {
                object =  selection[path[path.length - 1]];
            } else object = selection;

            const result = {};
            for (const entry of Object.entries(object)) {
                if (entry[0] !== 'alternativeLines') {
                    generateObject(result, entry);
                }
            }
            return result;
        },
        setHash(data, board) {
            const str = data.constructPreliminaryHashString(board);
            const hash = crc32(str);
            if (this.boardHashes.has(hash)) {
                this.boardHashes.set(hash, this.boardHashes.get(hash) + 1);
            } else {
                this.boardHashes.set(hash, 1);
            }
        },
        getHash(data, board) {
            const str = data.constructPreliminaryHashString(board);
            const hash = crc32(str);
            return this.boardHashes.has(hash) ? this.boardHashes.get(hash) : 0;
        },
        *[Symbol.iterator]() {
            function* getEntries(entries, path) {
                let object = {};
                let index = 0;
                let current = entries[index];

                while (index < entries.length) {
                    path.push(index);
                    let isAltGen = null;
                    for (const entry of Object.entries(current)) {
                        if (entry[0] === 'alternativeLines') {
                            isAltGen = entry[1].length ? entry[1] : false;
                        } else {
                            generateObject(object, entry);
                        }
                    }

                    object.path = path.slice();
                    yield object;
                    object = new Object();
                    if (isAltGen) {
                        yield lineSymbols.lineStart;
                        yield* getEntries(isAltGen, path);
                        yield lineSymbols.lineEnd;
                    }

                    [index, current] = [++index, entries[index]];
                    path.pop(index);
                }
            }

            yield* getEntries(this.moves, []);
        }
    };
}

export { lineSymbols, moveTypesSpecial, createMoveTree };