import { colorEnum } from "./Board";
import { topicLines } from "./Reporter";

const attackTypeEnum = {
    attackOnly: Symbol(),
    moveOnly: Symbol(),
    normal: Symbol(),
    rayGen: Symbol(),
    rayTrace: Symbol(),
    rayTraceLimited: Symbol(),
    isRayTrace(attackType) {
        return attackType === this.rayTrace || attackType === this.rayTraceLimited;
    }
};

class PieceControl {
    /*---------------------------------- INITIALIZATION -----------------------------------------*/
    static letterMap = new Map();

    constructor(pieceLetter, inferredOptions) {
        PieceControl.letterMap.set(pieceLetter, inferredOptions);
        this.moves = [];
        this.color = 0; // default
        this.sliding = [];
        this.jumping = [];
        this.rayGenCache = [];
        this.hooks = {
            useTrajectory: undefined,
            usePerspective: undefined,
            usePawnLogic: undefined,
            useHopping: undefined
        };

        this.reporter = null;
        this.letter = pieceLetter;
        this.moveLegalityCase = null;
        
        this.baseRankActive = false;
        this.immunePieces = [];

        this.extends = [];
    }

    static getConstructor(letter) {
        return PieceControl.letterMap.get(letter) ?? PieceControl;
    }

    setColor(color) {
        this.color = colorEnum[color] ?? 0;
    }

    setBaseImmunePieces(immunePieces) {
        this.immunePieces = immunePieces; // array [false, false, true, false] i.e.
    }

    setBaseRankActive(baseRank, x, y) {
        this.baseRankActive = [13 - y, x, y, 13 - x][this.color] === baseRank;
    }

    setCoordinates(board, fenOptions, [x, y]) {
        this.board = board;
        this.x = x;
        this.y = y;
        this.moves = [];
        this.fenOptions = fenOptions;
    }

    setReporter(reporter) {
        this.reporter = reporter;
        reporter.evaluateCase(reporter.createShallowCase(topicLines.moveGeneration, this.hooks, this));
        this.moveLegalityCase = reporter.createCase(topicLines.moveGenerationLegality);
    }

    /*---------------------------------- INITIALIZATION -----------------------------------------*/
    /*-------------------------------------------------------------------------------------------*/
    /*--------------------------------- MOVE GENERATION -----------------------------------------*/

    modifyDisplacements(x, y) {
        return [[x, y], [-y, x], [-x, -y], [y, -x]][this.color];
    }

    pushMove(x, y, isRayGen) {
        if (!isRayGen && this.hooks.usePawnLogic && this.hooks.usePawnLogic.promotionRanks) {
            if (this.hooks.usePawnLogic.promotionRanks[this.color] === (this.color % 2 === 0 ? y : x)) {
                this.moves.push([x, y, [this.hooks.usePawnLogic.promotionPieces ?? 'QRBN']]);
            } else {
                this.moves.push([x, y]);
            }
        } else {
            this.moves.push([x, y]);
        }
    }

    generateSlidingAttack(displacementX, displacementY, special = attackTypeEnum.normal, limit = Infinity, i = -1, rayGenCache = null) {
        let rayTraceLimit = 0;
        if (this.hooks.usePerspective && this.hooks.usePerspective[this.color]) {
            [displacementX, displacementY] = this.modifyDisplacements(displacementX, displacementY);
        }

        let startX = this.x + displacementX, startY = this.y + displacementY;
        const xLength = this.board[0].length, yLength = this.board.length;
        const isRayGen = rayGenCache || attackTypeEnum.isRayTrace(special);

        while (limit > 0 && startX >= 0 && startX < xLength && startY >= 0 && startY < yLength) {
            // TODO add trajectories?

            // Move possibility
            if (!this.getMovePossibility(startX, startY, special, rayGenCache)) {
                this.moveLegalityCase.tests.isBlocked = [true, false];
            } else this.moveLegalityCase.tests.isBlocked = [false, false];

            // Always delegate legality checks, except if it is raytracing, raytracing doesn't need legality checks.
            if (this.reporter.evaluateCase(this.moveLegalityCase, { inferredClass: this, arguments: {
                special,
                startX: this.x, startY: this.y
            }}, isRayGen)) {
                this.pushMove(startX, startY, isRayGen);
            } 

            // Raytracing for getting pinned pieces, AKA rayTraceLimited
            if (special === attackTypeEnum.rayTraceLimited && this.board[startY][startX].length !== 0) {
                rayTraceLimit++;
                if (rayTraceLimit === 2) break;
            }
            if (!attackTypeEnum.isRayTrace(special) && this.board[startY][startX].length !== 0) {
                break;
            }

            startX += displacementX;
            startY += displacementY;
            limit--;
        }
    }

    generateJumpAttack(displacementX, displacementY, special = attackTypeEnum.normal, i = -1, rayGenCache = null) {
        if (this.hooks.usePerspective && this.hooks.usePerspective[this.color]) {
            [displacementX, displacementY] = this.modifyDisplacements(displacementX, displacementY);
        }

        let startX = this.x + displacementX, startY = this.y + displacementY;
        const isRayGen = rayGenCache || attackTypeEnum.isRayTrace(special);
       
        if (this.hooks.useTrajectory && i !== -1) { // Bifuracted pieces, i.e. xiangqi horse
            const trajectory = this.hooks.useTrajectory[i];
            let trajectoryX, trajectoryY;
            if (this.hooks.usePerspective && this.hooks.usePerspective[this.color]) {
                [trajectoryX, trajectoryY] = this.modifyDisplacements(trajectory[0], trajectory[1]);
            } else {
                trajectoryX = trajectory[0], trajectoryY = trajectory[1];
            }
            // Check bifuraction possibility
            if (!this.getMovePossibility(this.x + trajectoryX, this.y + trajectoryY, special, rayGenCache)) { 
                this.moveLegalityCase.tests.isTrajectoryBlocked = [true, true];
            } else this.moveLegalityCase.tests.isTrajectoryBlocked = [false, true];
        }

    
        // Check move possibility
        if (!this.getMovePossibility(startX, startY, special, rayGenCache)) {
            this.moveLegalityCase.tests.isBlocked = [true, false];
        } else this.moveLegalityCase.tests.isBlocked = [false, false];
        if (this.reporter.evaluateCase(this.moveLegalityCase, { inferredClass: this, arguments: {
                special,
                startX, startY
            }}, isRayGen)) {
            this.pushMove(startX, startY, isRayGen);
        }
        this.reporter.resetCase(this.moveLegalityCase);
    }

    getMovePossibility(startX, startY, special = attackTypeEnum.normal, rayGenCache = null) {
        let modifiedX = startX, modifiedY = startY;
        if (!this.board[modifiedY] || this.board[modifiedY][modifiedX] === undefined) return false; // Out of bounds?

        const piece = this.board[modifiedY][modifiedX];
        // Make sure you don't capture immune zombie or your own piece, unless it is ray-genning to get legal moves.
        if (!rayGenCache && !attackTypeEnum.isRayTrace(special)) { 
            if (this.immunePieces[colorEnum[piece.charAt(0)] ?? null] ?? false) return false;
        }

        const pushTo = () => {
            if (rayGenCache) {
                this.rayGenCache.push(rayGenCache);
            }

            return true;
        };

        if (special === attackTypeEnum.normal) {
            if (piece.length !== 1) {
                return pushTo();
            }
        } else if (special === attackTypeEnum.rayGen) {
            if (piece.length !== 0) {
                return pushTo();
            }
        } else if (special === attackTypeEnum.attackOnly) {
            if (piece.length === 2) {
                return pushTo();
            }
        } else if (special === attackTypeEnum.moveOnly) {
            if (piece.length === 0) {
                return pushTo();
            }
        } else if (attackTypeEnum.isRayTrace(special)) {
            return pushTo();
        }

        return false;
    }

    /*--------------------------------- MOVE GENERATION -----------------------------------------*/
    /*-------------------------------------------------------------------------------------------*/
    /*------------------------------- OUTPUT GENERATION -----------------------------------------*/

    getPossibleCells() {
        if (this.hooks.useHopping) {
            this.sliding.forEach(a => this.generateSlidingAttack(a[0], a[1], attackTypeEnum.rayGen, Infinity, null, a.slice()));
            const moves = this.moves.slice();
            this.moves = [];
            moves.forEach((s, i) => {
                const startX = s[0] + this.rayGenCache[i][0];
                const startY = s[1] + this.rayGenCache[i][1];
                if (this.getMovePossibility(startX, startY)) {
                    this.moveLegalityCase.tests.isBlocked = [false, false];
                } else this.moveLegalityCase.tests.isBlocked = [true, false];

                if (this.reporter.evaluateCase(this.moveLegalityCase, { inferredClass: this, arguments: {
                    startX, startY,
                    special: attackTypeEnum.normal
                }})) {
                    this.moves.push([startX, startY]);
                }
                this.reporter.resetCase(this.moveLegalityCase);
            });
            this.rayGenCache = [];
        } else {
            this.sliding.forEach(a => this.generateSlidingAttack(a[0], a[1]));
            this.jumping.forEach(a => this.generateJumpAttack(a[0], a[1]));
        }
    }

    rayGenJumpingAttacks() {
        this.jumping.forEach(a => this.generateJumpAttack(a[0], a[1], attackTypeEnum.rayTrace));
        return this.moves.splice(0);
    }

    rayGenSlidingAttacks(trace) {
        const slidingLines = [];

        this.sliding.forEach(a => {
            this.generateSlidingAttack(a[0], a[1], trace ?? attackTypeEnum.normal);
            if (this.moves.length !== 0) {
                slidingLines.push([this.moves.splice(0)]);
            }
        });

        return slidingLines;
    }

    getPseudoLegalMoves() {
        this.extends.forEach(f => f());
        this.getPossibleCells();
        return this.moves.splice(0);
    }

    /*------------------------------- OUTPUT GENERATION -----------------------------------------*/
}

class RookControl extends PieceControl {
    constructor() {
        super("R", {
            construct: new.target,
            points: 5,
            isComplex: false,
            isJumping: false,
            isSliding: true
        });
        this.sliding = [[-1, 0], [1, 0], [0, 1], [0, -1]];
    }
}

