class PawnControl extends PieceControl {
    constructor() {
        super("P", {
            construct: new.target,
            points: 1,
            isComplex: false,
            isJumping: true,
            isSliding: false
        });
        this.hooks.usePerspective = Array(4).fill(true);
        this.hooks.usePawnLogic = true;
    }

    getPossibleCells() {
        this.generateJumpAttack(-1, -1, attackTypeEnum.attackOnly);
        this.generateJumpAttack(1, -1, attackTypeEnum.attackOnly);
        this.generateSlidingAttack(0, -1, attackTypeEnum.moveOnly, this.baseRankActive ? 2 : 1);
        return this.moves;
    }

    rayGenJumpingAttacks() {
        this.generateJumpAttack(-1, -1, attackTypeEnum.rayTrace);
        this.generateJumpAttack(1, -1, attackTypeEnum.rayTrace);
        const moves = this.moves.slice();
        this.moves = [];
        return moves;
    }
}

