import { colorEnum } from "../movegen/Board";
import { PromoteTo, PromotionRank } from "../movegen/Variant";

const binaryMasks = Array(14).fill(1).map((v, i) => v << 14 | v << (13 - i));

self.onmessage = ({ data: { board } }) => {
    // Step 1. Initialize the royals, get the optimal royal piece configuration.
    const possibleRoyals = [[], [], [], []];
    const boardSquares = board.getSquares(), options = board.getBoardData(),
        controls = board.getPieceControls(), reporter = board.getReporter();
   
    let promotionPieces = [], promotionRanks = [];
    reporter.getRules().forEach(r => {
        if (r instanceof PromoteTo) {
            promotionPieces = r.getData();
        } else if (r instanceof PromotionRank) {
            promotionRanks = r.getData();
        }
    });

    options.fenOptions.royal.forEach((r, i) => {
        if (r) {
            if (controls.has(boardSquares[r[1]][r[0]]) && controls.get(boardSquares[r[1]][r[0]]).hooks.usePawnLogic) {
                possibleRoyals[i] = [boardSquares[r[1]][r[0]].charAt(1), ...promotionPieces];
            } else possibleRoyals[i] = [boardSquares[r[1]][r[0]].charAt(1)];
        } else if (promotionPieces.includes("K")) {
            possibleRoyals = ["K"];
        }
    });

    const royalMoveSets = [
        ["", "W", "R", "E", "M", "Q", "D", "A"],
        ["", "F", "B", "H", "M", "Q", "D", "A", ""],
        ["Y", "Z"], ["I", "J"], ["S", "T"], ["S", "Y"], ["I", "Y"],
        ["U", "N", "O", "M", "H", "A", "E", "", "M", "V"], ["C", "L"], ["C", "V"]
    ];
    const royalPieceSets = Array.from({ length: 4 }, () => new Set());
    for (let i = 0; i < possibleRoyals.length; i++) {
        if (possibleRoyals[i].length > 1) {
            royalMoveSets.forEach(e => {
                for (let j = 0; j < e.length; j++) {
                    if (possibleRoyals[i].includes(e[j])) {
                        const targetArr = e.slice(j + 1);
                        possibleRoyals[i].forEach(v => { if (!targetArr.includes(v)) { royalPieceSets.add(v); } });
                        return;
                    }
                }

                possibleRoyals[i].forEach(v => royalPieceSets.add(v));
            });
        } else if (possibleRoyals[i].length === 1) {
            royalPieceSets[i].add(possibleRoyals[i][0]);
        }
    }
    performance.mark("insuffStart");
    // Step 2. Get all the co-ordinates that a single piece can reach, and what can promoted pieces reach.
    const boardDestinations = new Map();
    const walls = "Xx";
    const emptyBoard = boardSquares.map(row => row.map(square => walls.includes(square) ? square : ''));
    const virtualFenOptions = options.createVirtualCopy();
    const baseImmune = Array(4).fill(false);
    virtualFenOptions.fenOptions.royal = Array(4).fill(null);

    const getAllSquaresRecursively = (x, y, piece) => {
        const currentSquares = new Set(), unvisitedSquares = new Set();
        const control = controls.get(piece.charAt(1));
        control.setColor(colorEnum[piece.charAt(0)]);
        if (control.hooks.usePawnLogic && options.fenOptions.pawnBaseRank < 13) {
            control.setBaseRankActive(options.fenOptions.pawnBaseRank - 1, x, y);
        }
        control.setBaseImmunePieces(baseImmune);

        const recurse = (x, y) => {
            control.setCoordinates(emptyBoard, virtualFenOptions, [x, y]);
            const moves = control.getPseudoLegalMoves();
            if (moves.length === 0) return;
            moves.forEach(m => {
                const moveString = m[0] + ":" + m[1];
                if (!currentSquares.has(moveString)) {
                    unvisitedSquares.add(moveString);
                }
            });

            for (const value of unvisitedSquares) {
                const [x, y] = value.split(":").map(v => parseInt(v, 10));
                if (!currentSquares.has(value)) {
                    currentSquares.add(value);
                    recurse(x, y);
                }
            }
        };

        recurse(x, y);

        const permutations = new Uint16Array(14).fill(1 << 14);
        currentSquares.forEach(value => {
            const valueArr = value.split(":").map(v => parseInt(v, 10));
            permutations[valueArr[1]] |= binaryMasks[[valueArr[0]]];
        });
        unvisitedSquares.forEach(value => {
            const valueArr = value.split(":").map(v => parseInt(v, 10));
            permutations[valueArr[1]] |= binaryMasks[[valueArr[0]]];
        });
     
        return permutations;
    };

 
    for (let i = 0; i < boardSquares.length; i++) {
        for (let j = 0; j < boardSquares[0].length; j++) {
            if (boardSquares[i][j].length === 2 && !boardSquares[i][j].startsWith("d")) {
                const piece = boardSquares[i][j].charAt(1);
                const permutations = getAllSquaresRecursively(j, i, boardSquares[i][j]);
                if (boardDestinations.has(piece)) {
                    boardDestinations.set(piece, boardDestinations.get(piece).concat(permutations));
                } else {
                    boardDestinations.set(piece, [permutations]);
                }
            }
        }
    }
    

    for (let i = 0; i < promotionPieces.length; i++) {
        for (let j = 0; j < promotionRanks.length; j++) {
            const piece = promotionPieces[i];
            const x = 0, y = 0;
            if (j % 2 === 0) y = promotionRanks[j]; else x = promotionRanks[j];
            const constructedPieceString = "p" + colorEnum[j.toString()] + piece;

            const permutations = getAllSquaresRecursively(x, y, boardSquares[i][j]);
            if (boardDestinations.has(constructedPieceString)) {
                boardDestinations.set(constructedPieceString, boardDestinations.get(constructedPieceString).concat(permutations));
            } else {
                boardDestinations.set(constructedPieceString, [permutations]);
            }
        }
    }
    performance.mark("insuffEnd");
    performance.measure("measure1", "insuffStart", "insuffEnd");

    const registryIndexes = new Map();
    const registry = {};
    const entries = boardDestinations.entries();

    for (const entry of entries) {
        if (/[PG]/.test(entry[0])) continue;
        let isAlwaysIntersecting = true;
        const permutationArray = [];

        for (let i = 0; i < entry[1].length; i++) {
            if (permutationArray.length === 0) {
                permutationArray.push(entry[1][i]);
            } else {
                const index = permutationArray.findIndex(uintArr => uintArr.every((n, j) => n === entry[1][i][j]));
                if (index > -1) {
                    permutationArray.push(index);
                } else {
                    permutationArray.push(entry[1][i]);
                    isAlwaysIntersecting = false;
                }
            }
        }

        if (!isAlwaysIntersecting) {
            let baseArray = [];

            permutationArray.forEach(uintArray => {
                if (typeof uintArray === 'number') return;

                const uid = Symbol();
                if (baseArray.length === 0) {
                    uintArray.forEach(p => {
                        const newArr = [];
                        for (let i = 0; i < 14; i++) {
                            newArr.push((p & binaryMasks[i]) === binaryMasks[i] ? uid : null);
                        }
                        baseArray.push(newArr);
                    });
                } else {
                    uintArray.forEach((p, j) => {
                        for (let i = 0; i < 14; i++) {
                            if (!baseArray[j][i] && (p & binaryMasks[i]) === binaryMasks[i]) {
                                baseArray[j][i] = uid;
                            }
                        }
                    });
                }

                registry[uid] = uintArray;
            });

            registryIndexes.set(entry[0], baseArray);
        }
    }

  
    console.log(performance.getEntriesByType("measure"));
    // Step 3. Get all the possible squares the royal pieces can reach, and calculate the possible squares minus the opposition squares.
    // This will be a matrix of arrays of 2D arrays of a special object, with the covered squares being Uint16Arrays and the optimal square being an array of two numbers.
    const oppositionMatrix = Array.from({ length: 4 }, () => Array.from({ length: 4 }, () => null));
    const scanAttackSquares = (piece) => {
        const control = controls.get(piece);
        control.setColor(colorEnum[piece.charAt(0)]);
        if (control.hooks.usePawnLogic && options.fenOptions.pawnBaseRank < 13) {
            control.setBaseRankActive(options.fenOptions.pawnBaseRank - 1, x, y);
        }
        control.setBaseImmunePieces(baseImmune);

        const finalArray = Array.from({ length: 14 }, () => Array.from({ length: 14 }, () => null));
        for (let i = 0; i < boardSquares.length; i++) {
            for (let j = 0; j < boardSquares[0].length; i++) {
                if (emptyBoard[i][j].length === 0) {
                    control.setCoordinates(emptyBoard, virtualFenOptions, [x, y]);
                    const moves = control.getPseudoLegalMoves();
                    const baseArray = Array.from({ length: 14 }, () => Array.from({ length: 14 }, () => '0'));
                    moves.forEach(m => baseArray[m[1]][m[0]] = '1');
                    const permutations = new Uint16Array(14);
                    baseArray.forEach((r, i) => permutations[i] = parseInt(r.join(""), 2));
                    finalArray[i][j] = permutations;
                }
            }
        }

        return finalArray;
    };

    for (let i = 0; i < possibleRoyals.length; i++) {
        for (let j = 0; j < possibleRoyals[i].length; j++) {
            const royal = possibleRoyals[i][j];
            if (!oppositionMatrix[i][i]) oppositionMatrix[i][i] = [];
            oppositionMatrix[i][i].push(scanAttackSquares(royal));
        }
    }


    for (let i = 0; i < oppositionMatrix.length; i++) {
        for (let j = 0; j < oppositionMatrix[0].length; j++) {
            if (i === j) continue;

        }
    }
};