import { specialMovesEnum, colorEnum } from "../movegen/Board";
import createFENOptions from "./FENOptions";

const parseSingleCoordinate = (coordinate) => {
    if (coordinate.length === 0) return null;
    return [coordinate.charCodeAt(0) - 97, 14 - parseInt(coordinate.slice(1), 10)];
};

const parseEnPassantCoordinates = (coordinates) => {
    if (coordinates.length === 0) return null;
    const coordinatesArray = coordinates.split(":");
    return [[coordinatesArray[0].charCodeAt(0) - 97, 14 - parseInt(coordinatesArray[0].slice(1), 10)],
    [coordinatesArray[1].charCodeAt(0) - 97, 14 - parseInt(coordinatesArray[1].slice(1), 10)]];
};

const parsePGN4moves = (moves) => {
    const constants = {
        SPLIT: '.',
        PIECE_REGEX: /[xA-Z-a-n0-9-=+#]/,
        PIECE_REGEX_SIMPLIFIED: /[A-Z-a-n0-9-]/,
        BRACKETS: {
            COMMENT_START: '{',
            COMMENT_END: '}',
            VARIATION_START: '(',
            VARIATION_END: ')'
        }
    };

    const parseMoves = (moves, path) => {
        const moveList = [];
        const currentPath = path;

        const getMoveIndexes = (mI) => {
            while (moves[mI] && constants.PIECE_REGEX.test(moves[mI])) mI++;
            return mI;
        };

        const parseComment = (mI) => {
            while (moves[mI] && moves[mI] !== constants.BRACKETS.COMMENT_END) mI++;
            return mI;
        };

        const getEnumeratorIndex = (mI) => {
            while (moves[mI] && /\d|\./.test(moves[mI])) mI++;
            return --mI;
        };

        const findBracketIndex = (i) => {
            let depth = 0;
            for (; i < moves.length; i++) {
                switch (moves[i]) {
                    case constants.BRACKETS.COMMENT_START:
                        i = parseComment(i);
                        break;
                    case constants.BRACKETS.VARIATION_START:
                        depth++;
                        break;
                    case constants.BRACKETS.VARIATION_END:
                        if (--depth === 0) {
                            return i;
                        }
                        break;
                }
            }

            console.error("No closing matching parentheses");
            return i;
        };

        const setName = (move, name) => {
            if (name.endsWith(specialMovesEnum.resign)) move.type = specialMovesEnum.resign;
            if (name.endsWith(specialMovesEnum.timeout)) move.type = specialMovesEnum.timeout;
            if (name.endsWith(specialMovesEnum.stalemate)) move.type = specialMovesEnum.stalemate;
            if (name === specialMovesEnum.pass || move.type === specialMovesEnum.draw) move.type = name;
            const promotionMatch = name.match(/.*?(=[A-Zx])/);
            if (promotionMatch && promotionMatch[1]) {
                move.suffix = promotionMatch[1];
            }

            move.moveName = name;
            console.log(promotionMatch, move)
        }

        let currentMove = { alternativeLines: [] };
        let increment = -1, variationIncrement = -1;

        for (let i = 0; i < moves.length; i++) {
            let beNaN = isNaN(moves[i]);
            if (beNaN && !/\s/.test(moves[i]) && moves[i] !== constants.SPLIT) {
                if (constants.PIECE_REGEX_SIMPLIFIED.test(moves[i])) {
                    const indexes = getMoveIndexes(i);
                    setName(currentMove, moves.substring(i, indexes));
                    i = indexes - 1;
                } else if (moves[i] === constants.BRACKETS.COMMENT_START) {
                    const indexes = parseComment(i);
                    currentMove.comment = moves.substring(i + 1, indexes);
                    i = indexes;
                } else if (moves[i] === constants.BRACKETS.VARIATION_START) {
                    const index = findBracketIndex(i);
                    currentPath.push(increment + 1, ++variationIncrement);
                    currentMove.alternativeLines.push(parseMoves(moves.substring(i + 1, index), currentPath.slice()));
                    currentPath.pop();
                    currentPath.pop();
                    i = index;
                }
            } else if ((moves[i] === constants.SPLIT && moves[i + 1] && moves[i + 1] === constants.SPLIT)
                || (moves[i].trim() && !beNaN) && moves[i + 1] && (moves[i + 1] === constants.SPLIT || !isNaN(moves[i + 1]))) {
                if (!beNaN) i = getEnumeratorIndex(i);
                if (currentMove.moveName) {
                    currentPath.push(++increment);
                    currentMove.path = currentPath.slice();
                    currentPath.pop();
                    moveList.push(Object.assign({}, currentMove));
                }
                currentMove = { alternativeLines: [] };
            }
        }

        if (currentMove.moveName) {
            currentPath.push(++increment);
            currentMove.path = currentPath.slice();
            moveList.push(Object.assign({}, currentMove));
        }

        return moveList;
    }

    return parseMoves(moves, []);
};

const parseFEN4 = (fen4, addControlCallback) => {
    const data = createFENOptions();
    const board = [];

    const parts = fen4.split("-");
    if (parts.length < 7 || parts[parts.length - 1].split("/").length !== 14) {
        console.error("FEN has less than 7 parts");
    } else {
        data.sideToMove = colorEnum[parts[0].charAt(0).toLowerCase()];

        data.fenOptions.dead = parts[1].split(",").map(e => parseInt(e, 10));
        data.fenOptions.castleKingside = parts[2].split(",").map(e => parseInt(e, 10));
        data.fenOptions.castleQueenside = parts[3].split(",").map(e => parseInt(e, 10));
        data.points = parts[4].split(",").map(e => parseInt(e, 10));
        data.fenOptions.plyCount = parseInt(parts[5], 10);

        try {
            if (parts[6].startsWith("{")) {
                const extra = JSON.parse(parts[6].replaceAll("\(", '[').replaceAll(")", "]").replaceAll("'", '"'));
                ["enPassant", "zombieType", "zombieImmune", "pawnBaseRank", "wb", "boxOffset", "noCorners", "lives", "castleWith", "royal"]
                    .forEach(e => { if (extra[e]) data.fenOptions[e] = extra[e] });
                const res = [];
                if (extra.resigned) res.push(extra.resigned);
                if (extra.stalemated) res.push(extra.stalemated);
                if (extra.flagged) res.push(extra.flagged);
                data.fenOptions.resigned = res[0] ? res[0].map((_, i) => res.some(a => a[i])) : Array(4).fill(false);
                if (extra.dim) {
                    data.fenOptions.dimension = extra.dim.split("x").map(e => parseInt(e, 10));
                }
            }
        } catch (_) {
            fallback();
        }

        const fenPart = parts[6].startsWith("{") ? parts[7] : parts[6];
        const detectedRoyals = [null, null, null, null];
        fenPart.split("/").forEach((line, i) => {
            board.push([]);
            let j = 0;
            line.split(",").forEach((p) => {
                const altP = p.endsWith('"') ? p.slice(0, -1) : p;
                if (isNaN(altP)) {
                    board[i].push(altP);
                    if (!data.fenOptions.royal && !altP.startsWith("d") && altP.length !== 1) {
                        if (altP.charAt(1) === "K") {
                            detectedRoyals[colorEnum[altP.charAt(0)]] = [j, i];
                        }
                    }
                    addControlCallback(altP);
                } else {
                    board[i].push(...Array(parseInt(altP, 10)).fill(""));
                    j += parseInt(altP, 10) - 1;
                }
                j++;
            });
        });

        if (data.fenOptions.enPassant) {
            data.fenOptions.enPassant = data.fenOptions.enPassant.map(s => parseEnPassantCoordinates(s));
        } else data.fenOptions.enPassant = Array(4).fill(null);
        if (data.fenOptions.royal) {
            data.fenOptions.royal = data.fenOptions.royal.map(s => parseSingleCoordinate(s));
        } else data.fenOptions.royal = detectedRoyals;
        console.log(data.fenOptions)
    }

    return { board, data };
};

export { parseSingleCoordinate, parseEnPassantCoordinates, parsePGN4moves, parseFEN4 };