const topicLines = {
    moveGeneration: Symbol(), // Sideways Pawns, Torpedo, promotion
    moveGenerationLegality: Symbol(), // Any Capture, Stonewall
    fenOptions: Symbol(), // opposite-side castling, N-Check, Self-check, Teammate
    postMoveOptions: Symbol(), // Fatal Capture, OxN, P4M, Atomic, Points for Mate, Takeover, Stalemate settings, Bare piece rule
    mateHandling: Symbol(), // No DKW, KotH, Racing Kings, 
    moveValidation: Symbol(), // Giveaway, Munching Chess, CTK, Allow Passing, Racing Kings
    boardModification: Symbol() // Chess 960, paradigm chess30, alt. teams
};

class Reporter {
    constructor(topics) {
        this.topics = topics.reduce((object, line) => ({ ...object, [line]: [] }), {});
        this.cases = new WeakMap();
        this.rules = [];
    }

    addGameRules(rules) {
        rules.forEach(rv => {
            if (rv.extendCategory && this.topics[rv.extendCategory]) {
                this.topics[rv.extendCategory].push(rv);
            }
        });
        this.rules = rules;
    }

    getRules() { return this.rules; }

    getProxy() {
        return new Proxy({}, {
            set(target, prop, value) {
                if (!target[prop] || !target[prop].isFrozen || value === null) {
                    if (value === null) {
                        if (Reflect.has(target, prop)) {
                            Reflect.deleteProperty(target, prop);
                        }
                    } else {
                        const result = Reflect.set(target, prop, {
                            passed: !value[0],
                            isFrozen: value[1]
                        });
                        return Object.isExtensible(target) ? result : true;
                    }
                }
                return true;
            }
        });
    }

    createCase(topicLine) {
        if (!this.topics[topicLine]) return;
        const proxy = this.getProxy();
        const object = { uid: Symbol(), tests: proxy };
        this.cases.set(object, topicLine);
        return object;
    }

    createShallowCase(topicLine, inferredProxy, inferredClass) {
        if (!this.topics[topicLine]) return;
        const object = {
            uid: Symbol(),
            isShallow: true,
            proxyData: {
                baseObject: inferredProxy,
                baseClass: inferredClass,
            },
            classMutations: [],
            objectMutations: [],
            addClassMutation(callback) {
                this.classMutations.push(callback);
            },
            addObjectMutation(callback) {
                this.objectMutations.push(callback);
            }
        };
        this.cases.set(object, topicLine);
        return object;
    }

    evaluateCase(testCase, settings, ignoreOutput = false) {
        if (testCase.isShallow) {
            this.topics[this.cases.get(testCase)].forEach(rv => {
                if (rv.shallowEvaluate) rv.shallowEvaluate(testCase);
            });
            testCase.classMutations.forEach(m => m());
            testCase.objectMutations.forEach(m => m());
            this.resetCase(testCase);

        } else {
            if (!ignoreOutput) {
                this.topics[this.cases.get(testCase)].forEach(rv => {
                    if (rv.evaluate) rv.evaluate(testCase.tests, settings);
                });
            }

            const passed = Object.values(testCase.tests).every(p => p.passed);
            this.resetCase(testCase);
            return passed;
        }
    }

    evaluateCaseSuspended(testCase, settings, ignoreOutput = false) {
        if (testCase.isShallow) {
            this.topics[this.cases.get(testCase)].forEach(rv => {
                if (rv.shallowEvaluate) rv.shallowEvaluate(testCase);
            });

            return () => {
                testCase.classMutations.forEach(m => m());
                testCase.objectMutations.forEach(m => m());
                this.resetCase(testCase);
            };
        } else {
            const suspendedCases = [];
            if (!ignoreOutput) {
                this.topics[this.cases.get(testCase)].forEach(rv => {
                    if (rv.evaluate) {
                        if (rv.suspended) {
                            suspendedCases.push(rv);
                        } else {
                            rv.evaluate(testCase.tests, settings);
                        }
                    }
                });
            }

            return () => {
                if (!ignoreOutput) {
                    Object.preventExtensions(testCase.tests);
                    suspendedCases.forEach(rv => rv.evaluate(testCase.tests, settings));
                    const passed = Object.values(testCase.tests).every(p => p.passed);
                    this.resetCase(testCase);
                    return passed;
                }
            };
        }
    }

    resetCase(testCase) {
        if (testCase.isShallow) {
            testCase = {};
        } else {
            testCase.tests = this.getProxy();
        }  
    }
}

export { topicLines, Reporter };