import { colorEnum } from "../movegen/Board";
import { topicLines } from "../movegen/Reporter";

export default function createFENOptions() {
    const copySettings = (object, base) => {
        let val;
        if (typeof object === 'object') {
            if (object === null) val = null;
            else {
                if (Array.isArray(object)) {
                    val = object.map(copySettings);
                } else {
                    val = Object.keys(object).reduce((p, n) => {
                        p[n] = copySettings(object[n], base);
                        return p;
                    }, {});
                }
            }
        } else if (typeof object !== 'function') {
            val = object;
        } else {
            val = object.bind(base);
        }

        return val;
    };

    // The function has this set to the Board.js instance, and keeps the reference to the data object.
    function processRules(object, startX, startY, endX, endY, promotion) {
        const piece = promotion ? this.currentBoard[startY][startX].charAt(0).concat(promotion) : this.currentBoard[startY][startX];
        const endSquare = this.currentBoard[endY][endX];
        this.currentBoard[endY][endX] = piece;
        this.currentBoard[startY][startX] = '';
  
        object.fenOptions.royal = object.fenOptions.royal.map((r, i) => {
            if (r) {
                if (r[0] === startX && r[1] === startY) {
                    object.fenOptions.castleKingside[i] = false;
                    object.fenOptions.castleQueenside[i] = false;
                    return [endX, endY];
                } else return r.slice();
            } else return null;
        });

        if (object.fenOptions.enPassant && object.fenOptions.enPassant[object.sideToMove]) {
            object.fenOptions.enPassant[object.sideToMove] = '';
            if (object.fenOptions.enPassant.every(v => !v)) object.fenOptions.enPassant = undefined;
        }

        const { checkmates, stalemates } = this.getCheckmatesAndStalemates();
        const totalMateSum = checkmates.reduce((p, n) => n ? p + 20 : p, 0);
        const boardAlternations = {
            pointDistribution: {
                stalematePoints: stalemates.map((s, i) => s && i === object.sideToMove ? 20 : 0),
                checkmatePoints: checkmates.map((_, i) => i === object.sideToMove ? totalMateSum : 0),
                pieceCapturingPoints: Array(4).fill(0)
            },
            deadPieceList: [],
            takeoverPieceList: []
        };


        if (endSquare.length === 2) {
            boardAlternations.pointDistribution.pieceCapturingPoints[object.sideToMove] = this.controlsSettings.get(endSquare.charAt(1))?.points ?? 0;
        }

        const detectedPieces = this.getPlayerPieces();
        detectedPieces.forEach((p, i) => {
            if (checkmates[i] || (i === object.sideToMove && stalemates[i])) {
                p.forEach(piece => {
                    boardAlternations.deadPieceList.push(piece);
                });
                object.fenOptions.dead[i] = true;
            }
        });

        this.reporter.evaluateCase(this.reporter.createShallowCase(topicLines.fenOptions, boardAlternations, this));

        boardAlternations.deadPieceList.forEach(coord => { this.currentBoard[coord[1]][coord[0]] = 'd' + this.currentBoard[coord[1]][coord[0]].slice(1) });
        boardAlternations.takeoverPieceList.forEach(coord => { this.currentBoard[coord[1]][coord[0]] = colorEnum[(object.sideToMove).toString()] + this.currentBoard[coord[1]][coord[0]].slice(1) });
        if (this.gameType.isFFA()) {
            object.points = object.points.map((p, i) => p + boardAlternations.pointDistribution.stalematePoints[i]
            + boardAlternations.pointDistribution.checkmatePoints[i] + boardAlternations.pointDistribution.pieceCapturingPoints[i]);
        }
        
        do {
            object.sideToMove = object.sideToMove === 3 ? 0 : object.sideToMove + 1;
        } while (object.fenOptions.dead[object.sideToMove]);
    }

    const validatedFenOptions = {
        zombieType: Array(4).fill(null),
        royal: undefined
    };

    return {
        points: [0, 0, 0, 0],
        sideToMove: 0,
        fenOptions: {
            dead: Array(4).fill(false),
            resigned: Array(4).fill(false),
            enPassant: undefined,
            castleKingside: Array(4).fill(false),
            castleQueenside: Array(4).fill(false),
            castleWith: 'R',
            boxOffset: 0,
            zombieImmune: Array(4).fill(null),
            pawnBaseRank: 2,
            wb: false,
            dimension: [14, 14],
            noCorners: true,
            lives: Array(4).fill(null),
            plyCount: 0,
            get zombieType() {
                return validatedFenOptions.zombieType.every(v => !v) ? false : validatedFenOptions.zombieType;
            },
            set zombieType(v) { validatedFenOptions.zombieType = v },
            get royal() { return validatedFenOptions.royal },
            set royal(v) {
                validatedFenOptions.royal = v.filter((v) => {
                    if (!v || typeof v === 'string') return true;
                    else return v[0] >= 0 && v[0] < 14 && v[1] >= 0 && v[1] < 14;
                });
            }
        },
        constructPreliminaryHashString(board) {
            const builder = '';
            builder += this.sideToMove;
            builder = this.points.reduce((p, n) => p + '' + n, builder);
            builder = this.fenOptions.castleKingside.reduce((p, n) => p + (n ? '1' : '0'), builder);
            builder = this.fenOptions.castleQueenside.reduce((p, n) => p + (n ? '1' : '0'), builder);
            builder = this.fenOptions.lives.reduce((p, n) => p + '' + (n ?? ''), builder);
            builder = this.fenOptions.dead.reduce((p, n) => p + (n ? '1' : '0'), builder);
            builder = this.fenOptions.resigned.reduce((p, n) => p + (n ? '1' : '0'), builder);
            builder = this.fenOptions.resigned.reduce((p, n) => p + (n ? '1' : '0'), builder);
            builder = this.fenOptions.enPassant ? this.fenOptions.enPassant.reduce((p, n) => p + '' + (n ?? ''), builder) : builder;
            builder = board.reduce((p, n) => p + n.join("-"), builder);
            return builder;
        },
        createVirtualEffect(board, startX, startY, endX, endY, promotion = null) {
            const newObject = copySettings(this, this);
            processRules.call(board, newObject, startX, startY, endX, endY, promotion, suppliedArguments);
            return newObject;
        },
        createVirtualCopy() {
            return copySettings(this, this);
        },
        affectOptions(board, startX, startY, endX, endY, promotion = null) {
            processRules.call(board, this, startX, startY, endX, endY, promotion);
        }
    };
};