import { attackTypeEnum } from './PieceControl';
import { topicLines } from './Reporter';
import { colorEnum } from './Board';

const ruleList = {};
const regExRules = {};

class SidewaysPawns {
    constructor() {
        ruleList["SidewaysPawns"] = this;
        this.extendCategory = topicLines.moveGeneration;
    }

    shallowEvaluate(object) {
        if (object.proxyData.baseClass.hooks.usePawnLogic) {
            object.addClassMutation(() => {
                object.proxyData.baseClass.extends.push(function () {
                    this.generateJumpAttack(-1, 0, attackTypeEnum.moveOnly);
                    this.generateJumpAttack(1, 0, attackTypeEnum.moveOnly);
                }.bind(object.proxyData.baseClass));
            });
        }
    }
}

class Torpedo {
    constructor() {
        ruleList["Torpedo"] = this;
        this.extendCategory = topicLines.moveGeneration;
    }

    shallowEvaluate(object) {
        if (object.proxyData.baseClass.hooks.usePawnLogic) {
            object.addClassMutation(() => {
                object.proxyData.baseClass.extends.push(function () {
                    this.baseRankActive = true;
                }.bind(object.proxyData.baseClass));
            });
        }
    }
}

class AnyCapture {
    constructor() {
        ruleList["AnyCapture"] = this;
        this.extendCategory = topicLines.moveGenerationLegality;
    }

    evaluate(proxy, settings) {
        const { startX, startY, special } = settings.arguments;
        const { board, fenOptions } = settings.inferredClass;

        if (startX != null && startY != null) {
            if (special === attackTypeEnum.moveOnly) return;
            if (!board[startY] || !board[startY][startX]) return;

            const piece = board[startY][startX];
            if (piece.length === 2) {
                const color = colorEnum[piece.charAt(0)];
                if (color && fenOptions.resigned[color] && fenOptions.zombieImmune[color]) {
                    proxy.isBlocked = [true, true];
                } else {
                    if (!!fenOptions.royal[color] && fenOptions.royal[color][0] === startX
                        && fenOptions.royal[color][1] === startY) proxy.isBlocked = [true, true];
                    else proxy.isBlocked = [false, false];
                }
            }
        }
    }
}

class PromotionRank {
    constructor(match = undefined) {
        if (!match) regExRules['Prom=(\\d\\d?)'] = new.target;
        else if (match[1]) {
            this.promotionRank = parseInt(match[1], 10) - 1;
        }

        this.extendCategory = topicLines.moveGeneration;
    }

    shallowEvaluate(object) {
        const hooks = object.proxyData.baseObject;
        if (hooks.usePawnLogic) {
            object.addObjectMutation(() => {
                if (this.promotionRank === 98) {
                    if (typeof hooks.usePawnLogic !== 'object') {
                        hooks.usePawnLogic = { promotionRanks: false };
                    } else {
                        hooks.usePawnLogic.promotionRanks = false;
                    }
                } else {
                    const promotionRanks = [13 - this.promotionRank, this.promotionRank, this.promotionRank, 13 - this.promotionRank];
                    if (typeof hooks.usePawnLogic !== 'object') {
                        hooks.usePawnLogic = { promotionRanks };
                    } else {
                        hooks.usePawnLogic.promotionRanks = promotionRanks;
                    }
                }
            });
        }
    }

    getData() {
        return [13 - this.promotionRank, this.promotionRank, this.promotionRank, 13 - this.promotionRank];
    }
}

class PromoteTo {
    constructor(match = undefined) {
        if (!match) regExRules['PromoteTo=([A-Zx]+)'] = new.target;
        else if (match[1]) {
            this.promotionPieces = match[1].split("");
        }

        this.extendCategory = topicLines.moveGeneration;
    }

    shallowEvaluate(object) {
        const hooks = object.proxyData.baseObject;
        if (hooks.usePawnLogic) {
            object.addObjectMutation(() => {
                if (typeof hooks.usePawnLogic !== 'object') {
                    hooks.usePawnLogic = { promotionPieces: this.promotionPieces };
                } else {
                    hooks.usePawnLogic.promotionPieces = this.promotionPieces;
                }
            });
        }
    }

    getData() {
        return this.promotionPieces;
    }
}

class AlternativeTeams {
    constructor(match = undefined) {
        if (!match) regExRules['Teammate=(1|3)'] = new.target;
        else if (match[1]) {
            this.teammate = parseInt(match[1], 10);
        }

        this.extendCategory = topicLines.boardModification;
    }

    shallowEvaluate(object) {
        const settings = object.proxyData.baseObject;
        if (!settings.isFFA()) {
            object.addObjectMutation(() => {
                if (this.teammate === 1) {
                    settings.teamSettings.firstTeamColors = [true, true, false, false];
                    settings.teamSettings.secondTeamColors = [false, false, true, true];
                } else {
                    settings.teamSettings.firstTeamColors = [true, false, false, true];
                    settings.teamSettings.secondTeamColors = [false, true, true, false];
                }
            });
        }
    }
}

[SidewaysPawns, Torpedo, AnyCapture, PromotionRank, PromoteTo, AlternativeTeams].forEach(rv => Reflect.construct(rv, []));
const parseGameRules = (rules) => {
    const finalList = [];
    const ruleList = rules.split(/\s+/);
    const regExRuleSet = new Set(Object.keys(regExRules));

    ruleList.forEach(rv => {
        if (!ruleList[rv]) {
            regExRuleSet.forEach(regex => {
                const match = rv.match(new RegExp(regex));
                if (match) {
                    finalList.push(new (regExRules[regex])(match));
                    regExRuleSet.delete(regex);
                }
            });
        } else {
            finalList.push(ruleList[rv]);
        }
    });

    return finalList;
};

export { SidewaysPawns, Torpedo, AnyCapture, PromotionRank, PromoteTo, AlternativeTeams, ruleList, parseGameRules };