import { createMoveTree } from "../utils/MoveTree";
import { parseEnPassantCoordinates, parsePGN4moves, parseSingleCoordinate, parseFEN4 } from "../utils/Parsers";
import * as Pieces from "./PieceControl";
import { Reporter, topicLines } from "./Reporter";
import { parseGameRules } from "./Variant";

const colorEnum = {
    get r() { return 0 },
    get b() { return 1 },
    get y() { return 2 },
    get g() { return 3 },
    get Red() { return 0 },
    get Blue() { return 1 },
    get Yellow() { return 2 },
    get Green() { return 3 },
    get '0'() { return 'r' },
    get '1'() { return 'b' },
    get '2'() { return 'y' },
    get '3'() { return 'g' }
};

const specialMovesEnum = {
    get resign() { return 'R' },
    get stalemate() { return 'S' },
    get draw() { return 'D' },
    get pass() { return 'P' },
    get timeout() { return 'T' }
};

const variantTypes = {
    solo: Symbol(),
    ffa: Symbol(),
    teams: Symbol()
};

const baseImmunes = Array(4).fill(false);

const createBasePreGeneratedAttacks = () => {
    return {
        hoppingPieces: Array.from({ length: 14 }, () => Array.from({ length: 14 }, () => 0)),
        slidingPieces: Array.from({ length: 14 }, () => Array.from({ length: 14 }, () => 0)),
        hoppingPieceLines: [],
        slidingPiecesLines: [],
        slidingPiecesRayTracing: [],
        attackingColors: Array.from({ length: 14 }, () => Array.from({ length: 14 }, () => 0))
    };
};

class Board {
    // type = static, game, puzzle
    constructor(type, pgn4) {
        this.controls = new Map();
        this.controlsSettings = new Map();
        this.data = {};
        this.moves = createMoveTree();
        this.preGeneratedAttacks = createBasePreGeneratedAttacks();
        this.board = [];
        this.boardMemo = [];
        this.isComplexSetup = {
            hasComplexPieces: false,
            hasComplexRules: false,
            isComplex() { return this.hasComplexPieces || this.hasComplexRules; }
        };
        this.reporter = new Reporter(Object.values(topicLines));

        this.gameData = {
            gameNumber: undefined,
            timeControl: undefined,
            players: [{}, {}, {}, {}],
            site: undefined,
            date: undefined,
            result: undefined,
            termination: undefined
        };
        this.gameType = {
            type: variantTypes.ffa,
            teamSettings: {
                firstTeamColors: undefined,
                secondTeamColors: undefined
            },
            getBaseColors(color) {
                if (this.isFFA()) {
                    return Array(4).fill(undefined).map((_, i) => i === color);
                } else {
                    return this.teamSettings.firstTeamColors[color] ? this.teamSettings.firstTeamColors : this.teamSettings.secondTeamColors;
                }
            },
            isFFA() {
                return this.type === variantTypes.solo || this.type === variantTypes.ffa;
            }
        };

        let pgn4Tags, pgn4Moves;
        const match = pgn4.match(/(?=1\.\s*?[xA-Z-a-n0-9-])/);
        if (match?.index) {
            pgn4Moves = pgn4.substring(match.index);
            pgn4Tags = pgn4.substring(0, match.index).split("]");

            this.moves.moves = parsePGN4moves(pgn4Moves);
        } else {
            pgn4Tags = pgn4.split("]");
        }

        const tagFunctions = {
            ['StartFen4']: {
                func: (fen4) => {
                    ({ board: this.board, data: this.data } = parseFEN4(fen4, (letter) => {
                        if (letter.startsWith("d") || letter.length === 1) return;
                        const realLetter = letter.charAt(1);
                        if (this.controls.has(realLetter)) return;
                        const information = Pieces.PieceControl.getConstructor(realLetter);
                        const control = Reflect.construct(information.construct, []);
                        control.setReporter(this.reporter);
                        this.controls.set(realLetter, control);
                        this.controlsSettings.set(realLetter, information);

                        if (information.isComplex) this.isComplexSetup.hasComplexPieces = true;
                    }));
                },
                arguments: null
            },
            ['RuleVariants']: {
                func: (rules) => {
                    const list = parseGameRules(rules);
                    this.reporter.addGameRules(list);
                },
                arguments: null
            },
            ['Variant']: {
                func: (variant) => {
                    switch (variant) {
                        case "FFA":
                            this.gameType.type = variantTypes.ffa;
                            break;
                        case "Solo":
                            this.gameType.type = variantTypes.solo;
                            break;
                        case "Teams":
                            this.gameType.type = variantTypes.teams;
                            this.gameType.teamSettings.firstTeamColors = [true, false, true, false];
                            this.gameType.teamSettings.secondTeamColors = [false, true, false, true];
                    }
                },
                arguments: null
            },
            ['Date']: {
                func: (date) => { this.gameData.date = new Date(date); },
                arguments: null
            },
            ['GameNr']: {
                func: (number) => { if (!isNaN(number)) this.gameData.gameNumber = parseInt(number, 10); },
                arguments: null
            },
            ['Termination']: {
                func: (termination) => { this.gameData.termination = termination; },
                arguments: null
            },
            ['Site']: {
                func: (site) => { this.gameData.site = site; },
                arguments: null
            },
            ['TimeControl']: {
                func: (tc) => { this.gameData.timeControl = tc; },
                arguments: null
            }
        };

        const parsePlayerMatch = (match, content) => {
            if (match[2] !== undefined) {
                this.gameData.players[colorEnum[match[1]]].elo = parseInt(content, 10);
            } else {
                this.gameData.players[colorEnum[match[1]]].name = content;
            }
        };

        const leftFunctions = new Set(Object.keys(tagFunctions));

        pgn4Tags.forEach(tag => {
            let isPresent = false;
            for (const v of leftFunctions) {
                const tagStart = `[${v} "`;
                if (tag.startsWith(tagStart)) {
                    isPresent = true;
                    tagFunctions[v].arguments = tag.replace(tagStart, "").slice(0, -1);
                    leftFunctions.delete(v);
                    break;
                }
            }

            if (!isPresent) {
                const playerMatch = tag.match(/\[(Red|Blue|Yellow|Green)(Elo)?\s*?"/);
                if (playerMatch) parsePlayerMatch(playerMatch, tag.replace(playerMatch[0], "").slice(0, -1));
            }
        });

        const parseTag = (obj) => { if (obj.arguments) obj.func.call(this, obj.arguments); };
        parseTag(tagFunctions['Variant']);
        parseTag(tagFunctions['StartFen4']);
        parseTag(tagFunctions['RuleVariants']);
        parseTag(tagFunctions['Termination']);
        parseTag(tagFunctions['Date']);
        parseTag(tagFunctions['GameNr']);
        parseTag(tagFunctions['Site']);
        parseTag(tagFunctions['TimeControl']);

        this.reporter.evaluateCase(this.reporter.createShallowCase(topicLines.boardModification, this.gameType, this));
        this.currentBoard = this.board;
        this.pregenerateAttacks();

        this.insufficientMaterialModule = null;
        const insufficientMaterialGenerationWorker = new Worker(new URL("../utils/InsufficientMaterialGeneration.js", import.meta.url));
        insufficientMaterialGenerationWorker.postMessage({ board: this });
    }

    createVirtualCopy() {
        const excludedObjects = new WeakSet([this.data, this.board, this.boardMemo, this.isComplexSetup, this.preGeneratedAttacks, this.insufficientMaterialModule]);
        const targetObject = {};
        for (const property of Object.getOwnPropertyNames(Board.prototype)) {
            if (property === 'constructor') continue;
            targetObject[property] = Board.prototype[property].bind(targetObject);
        }

        for (const field of Object.getOwnPropertyNames(this)) {
            if (typeof this[field] === 'object') {
                if (excludedObjects.has(this[field])) {
                    excludedObjects.delete(this[field]);
                } else {
                    targetObject[field] = this[field];
                }
            } else {
                targetObject[field] = this[field];
            }
        }

        targetObject.board = [];
        targetObject.boardMemo = [];
        targetObject.isComplexSetup = Object.assign({}, this.isComplexSetup);
        targetObject.preGeneratedAttacks = createBasePreGeneratedAttacks();
        targetObject.data = this.data.createVirtualCopy();
        targetObject.insufficientMaterialModule = this.insufficientMaterialModule;

        return targetObject;
    }

    getSquares() { return this.board; }
    getReporter() { return this.reporter; }
    getPieceControls() { return this.controls; }
    getBoardData() { return this.data; }

    isKingInCheck(baseColor, fenRoyal = this.data.fenOptions.royal) {
        if (!fenRoyal[baseColor]) return false;
        if (this.isComplexSetup) {
            for (let i = 0; i < 14; i++) {
                for (let j = 0; j < 14; j++) {
                    const square = this.currentBoard[i][j];
                    if (square.length !== 2 || square.startsWith("d")) continue;
                    const color = square.charAt(0);
                    if (colorEnum[color] === baseColor) continue;
                    const control = this.controls.get(square.charAt(1));
                    control.setColor(color);
                    control.baseRankActive = false;
                    control.setCoordinates(this.currentBoard, this.data.fenOptions, [j, i]);
                    control.immunePieces = baseImmunes;
                    const threats = control.getPseudoLegalMoves();
                    if (threats.some(a => a.every((v, i) => v === fenRoyal[baseColor][i]))) return true;
                }
            }

            return false;
        } else {
            const coordinates = fenRoyal[baseColor];
            return this.preGeneratedAttacks.slidingPieces[coordinates[1]][coordinates[0]]
                || this.preGeneratedAttacks.hoppingPieces[coordinates[1]][coordinates[0]];
        }
    }

    getPlayerPieces() {
        const playerPieces = [[], [], [], []];

        for (let i = 0; i < 14; i++) {
            for (let j = 0; j < 14; j++) {
                const square = this.currentBoard[i][j];
                const color = colorEnum[square.charAt(0)];
                if (color !== undefined) playerPieces[color].push([j, i]);
            }
        }

        return playerPieces;
    }

    getCheckmatesAndStalemates() {
        const kingChecks = Array(4).fill(false);
        const legalMoves = Array(4).fill(false);
        const playerPieces = this.getPlayerPieces();

        for (let i = 0; i < playerPieces[this.data.sideToMove].length; i++) {
            const move = playerPieces[this.data.sideToMove][i];
            if (this.getLegalMoves(move[0], move[1]).length) {
                legalMoves[this.data.sideToMove] = true;
                break;
            }
        }
        if (this.isKingInCheck(this.data.sideToMove)) kingChecks[this.data.sideToMove] = true;

        const virtualCopy = this.createVirtualCopy();
        virtualCopy.board = this.board;
        virtualCopy.boardMemo = this.boardMemo;
        virtualCopy.currentBoard = virtualCopy.board;
        playerPieces.forEach((moves, p) => {
            if (p === this.data.sideToMove) return;
            if (!virtualCopy.isComplexSetup.isComplex()) virtualCopy.pregenerateAttacks(p);
            for (let i = 0; i < moves.length; i++) {
                if (virtualCopy.getLegalMoves(moves[i][0], moves[i][1], p).length) {
                    legalMoves[p] = true;
                    break;
                }
                if (virtualCopy.isKingInCheck(p)) kingChecks[p] = true;
            }
        });

        return {
            checkmates: legalMoves.map((move, i) => !move && kingChecks[i]),
            stalemates: legalMoves.map((move, i) => !move && !kingChecks[i])
        };
    }

    runComplexLegalityEvaluationChecks() {
        if (this.isComplexSetup.hasComplexRules) return;
        for (let i = 0; i < 14; i++) {
            for (let j = 0; j < 14; j++) {
                const square = this.currentBoard[i][j];
                const piece = colorEnum[square.charAt(1)];
                if (piece && this.controlsSettings.get(piece) && this.controlsSettings.get(piece).isComplex) {
                    this.isComplexSetup.hasComplexPieces = true;
                    return;
                } 
            }
        }
    }

    makeMove(_startX, _startY, _endX, _endY, _promotion = null) {
        this.data.affectOptions(this, ...arguments);
        this.pregenerateAttacks();
        this.runComplexLegalityEvaluationChecks();
    }

    /**
     * Futer benchmarks:
     * Std: 30 moves per second
     * Atomic & racing with xiangqi horses: 15 moves per second
     */

    makeVirtualMove(_startX, _startY, _endX, _endY, _promotion = null) {
        this.boardMemo = this.board.map(r => r.slice()).slice();
        this.currentBoard = this.boardMemo;
        this.runComplexLegalityEvaluationChecks();
        return this.data.createVirtualEffect(this, ...arguments);
    }

    pregenerateAttacks(sideToMove = this.data.sideToMove) {
        this.preGeneratedAttacks = createBasePreGeneratedAttacks();
        for (let i = 0; i < 14; i++) {
            for (let j = 0; j < 14; j++) {
                const square = this.currentBoard[i][j];
                if (square.length !== 2 || square.startsWith("d")) continue;
                const color = square.charAt(0);
                if (colorEnum[color] === sideToMove) continue;
                const letter = square.charAt(1);
                const control = this.controls.get(square.charAt(1));
                control.setColor(color);
                control.baseRankActive = false;
                control.setCoordinates(this.currentBoard, this.data.fenOptions, [j, i]);
                control.setBaseImmunePieces(Array(4).fill(false));

                if (this.controlsSettings.get(letter).isJumping) {
                    control.rayGenJumpingAttacks().forEach(a => {
                        this.preGeneratedAttacks.hoppingPieces[a[1]][a[0]]++;
                        this.preGeneratedAttacks.hoppingPieceLines.push([[j, i], a]);
                        const l = this.preGeneratedAttacks.attackingColors[a[1]][a[0]];
                        if (l !== -1 && this.preGeneratedAttacks.attackingColors[a[1]][a[0]] !== colorEnum[color] + 1) {
                            this.preGeneratedAttacks.attackingColors[a[1]][a[0]] = l === 0 ? 1 + colorEnum[color] : -1;
                        }
                    });
                }

                if (this.controlsSettings.get(letter).isSliding) {
                    const attacks = control.rayGenSlidingAttacks();
                    attacks.forEach(a => {
                        a.forEach(sa => {
                            sa.forEach(ssa => {
                                this.preGeneratedAttacks.slidingPieces[ssa[1]][ssa[0]]++;
                                const l = this.preGeneratedAttacks.attackingColors[ssa[1]][ssa[0]];
                                if (l !== -1 && this.preGeneratedAttacks.attackingColors[ssa[1]][ssa[0]] !== colorEnum[color] + 1) {
                                    this.preGeneratedAttacks.attackingColors[ssa[1]][ssa[0]] = l === 0 ? 1 + colorEnum[color] : -1;
                                }
                            });
                        });
                        this.preGeneratedAttacks.slidingPiecesLines.push([[j, i], ...a]);
                    });

                    control.rayGenSlidingAttacks(Pieces.attackTypeEnum.rayTraceLimited).forEach(a => {
                        this.preGeneratedAttacks.slidingPiecesRayTracing.push([[j, i], ...a]);
                    });
                }
            }
        }
    }

    isTheMoveLegal(color, attackX, attackY, pieceX, pieceY, promotion = null) {
        if (this.isComplexSetup.isComplex()) {
            const virtualFEN = this.makeVirtualMove(pieceX, pieceY, attackX, attackY, promotion);
            const check = this.isKingInCheck(color, virtualFEN.fenOptions.royal);
            this.currentBoard = this.board;
            this.runComplexLegalityEvaluationChecks();
            return check;
        } else {
            const [royalX, royalY] = this.data.fenOptions.royal[color];
            const isRoyalMove = royalX === pieceX && royalY === pieceY;
            const attackers = this.preGeneratedAttacks.hoppingPieces[royalY][royalX] +
                this.preGeneratedAttacks.slidingPieces[royalY][royalX];
            const targetSafety = this.preGeneratedAttacks.slidingPieces[attackY][attackX]
                + this.preGeneratedAttacks.hoppingPieces[attackY][attackX] === 0;
            const legalitySettings = {
                canKingCapture: undefined
            };

            for (let i = 0; i < this.data.fenOptions.royal.length; i++) {
                if (i !== color && this.data.fenOptions.royal[i] && this.data.fenOptions.royal[i][0] === attackX
                    && this.data.fenOptions.royal[i][1] === attackY) {
                    const royalDefendedSquare = this.preGeneratedAttacks.attackingColors[this.data.fenOptions.royal[i][1]][this.data.fenOptions.royal[i][0]];
                    if ((royalDefendedSquare === i + 1 || royalDefendedSquare === 0)
                        && (isRoyalMove || this.preGeneratedAttacks.attackingColors[royalY][royalX] === i + 1)) {
                        legalitySettings.canKingCapture = i + 1;
                    }
                }
            }

            for (let lineWrapIndex = 0; lineWrapIndex < this.preGeneratedAttacks.slidingPiecesRayTracing.length; lineWrapIndex++) {
                const [attackingPiece, attackLine] = this.preGeneratedAttacks.slidingPiecesRayTracing[lineWrapIndex];
                let isPieceUnderAttack = false, isRoyalUnderAttack = false, isTheTargetSquarePinListed = false;
                for (let attackIndex = 0; attackIndex < attackLine.length; attackIndex++) {
                    const [attackedX, attackedY] = attackLine[attackIndex];
                    if (!isPieceUnderAttack && attackedX === pieceX && attackedY === pieceY) isPieceUnderAttack = true;
                    else if (!isTheTargetSquarePinListed && isPieceUnderAttack && !isRoyalUnderAttack &&
                        attackedX === attackX && attackedY === attackY) isTheTargetSquarePinListed = true;
                    if (!isRoyalUnderAttack && attackedX === royalX && attackedY === royalY) isRoyalUnderAttack = true;

                    if (isRoyalMove) {
                        if (isRoyalUnderAttack && attackedX === attackX && attackedY === attackY) {
                            if (legalitySettings.canKingCapture > 0 && colorEnum[this.currentBoard[attackedY][attackedX].charAt(0)] + 1 === legalitySettings.canKingCapture) continue;
                            return false;
                        }
                    } else {
                        if (isPieceUnderAttack && isRoyalUnderAttack && attackingPiece[0] !== attackX
                            && attackingPiece[1] !== attackY && !isTheTargetSquarePinListed) {
                            if (legalitySettings.canKingCapture > 0 && colorEnum[this.currentBoard[attackedY][attackedX].charAt(0)] + 1 === legalitySettings.canKingCapture) continue;
                            return false;
                        }
                    }
                }
            }

            if (attackers > 1) {
                return isRoyalMove ? targetSafety || legalitySettings.canKingCapture : legalitySettings.canKingCapture;
            } else if (attackers === 1) {
                if (!isRoyalMove) {
                    if (this.preGeneratedAttacks.hoppingPieces[royalY][royalX] === 1) {
                        for (let lineWrapIndex = 0; lineWrapIndex < this.preGeneratedAttacks.hoppingPieceLines.length; lineWrapIndex++) {
                            const [attackingPiece, attackCoordinates] = this.preGeneratedAttacks.hoppingPieceLines[lineWrapIndex];

                            if (attackCoordinates[0] === royalX && attackCoordinates[1] === royalY
                                && attackingPiece[0] === attackX && attackingPiece[1] === attackY) {
                                return true;
                            }
                        }

                        return legalitySettings.canKingCapture;
                    } else {
                        for (let lineWrapIndex = 0; lineWrapIndex < this.preGeneratedAttacks.slidingPiecesLines.length; lineWrapIndex++) {
                            const [attackingPiece, attackLine] = this.preGeneratedAttacks.slidingPiecesRayTracing[lineWrapIndex];
                            let isRoyalUnderAttack = false, isTheTargetSquareBlocked = false;
                            for (let attackIndex = 0; attackIndex < attackLine.length; attackIndex++) {
                                const [attackedX, attackedY] = attackLine[attackIndex];
                                if (!isRoyalUnderAttack && attackedX === royalX && attackedY === royalY) isRoyalUnderAttack = true;
                                else if (!isTheTargetSquareBlocked && !isRoyalUnderAttack &&
                                    attackedX === attackX && attackedY === attackY) isTheTargetSquareBlocked = true;
                        
                                if (isRoyalUnderAttack && !(attackingPiece[0] === attackX
                                    && attackingPiece[1] === attackY) && !isTheTargetSquareBlocked) {
                                    if (legalitySettings.canKingCapture > 0 && colorEnum[this.currentBoard[attackedY][attackedX].charAt(0)] + 1 === legalitySettings.canKingCapture) continue;
                                    return legalitySettings.canKingCapture;
                                }
                            }
                        }

                        return true;
                    }
                } else {
                    return targetSafety || legalitySettings.canKingCapture;
                }
            } else {
                return isRoyalMove ? targetSafety || legalitySettings.canKingCapture : true;
            }
        }
    }

    getLegalMoves(x, y, baseColor = this.data.sideToMove) {
        if (this.currentBoard[y] && this.currentBoard[y][x] && this.currentBoard[y][x].length === 2) {
            const piece = this.currentBoard[y][x];
            const color = piece.charAt(0);
            const control = this.controls.get(piece.charAt(1));
            control.setColor(color);
            if (control.color !== baseColor) return [];
            if (control.hooks.usePawnLogic && this.data.fenOptions.pawnBaseRank < 13) {
                control.setBaseRankActive(this.data.fenOptions.pawnBaseRank - 1, x, y);
            }
            control.setCoordinates(this.currentBoard, this.data.fenOptions, [x, y]);
            control.setBaseImmunePieces(this.gameType.getBaseColors(colorEnum[color]));

            const pseudoLegalMoves = control.getPseudoLegalMoves();
            if (this.data.fenOptions.royal[colorEnum[color]]) {
                return pseudoLegalMoves.filter(m => {
                    return this.isTheMoveLegal(colorEnum[color], m[0], m[1], x, y);
                });
            } else {
                return pseudoLegalMoves;
            }
        } else {
            return [];
        }
    }
}

export { Board, colorEnum, specialMovesEnum };